      .navbar .container .navbar-panel #nav-icon4 {
        width: 60px;
        height: 45px;
        position: relative;
        display: none;
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
        -webkit-transition: .5s ease-in-out;
