        -o-transform-origin: left center;
        transform-origin: left center; }
      .navbar .container .navbar-panel #nav-icon4.open span:nth-child(1) {
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg);
        top: -3px;
        left: 8px; }
      .navbar .container .navbar-panel #nav-icon4.open span:nth-child(2) {
        width: 0%;
        opacity: 0; }
      .navbar .container .navbar-panel #nav-icon4.open span:nth-child(3) {
        -webkit-transform: rotate(-45deg);
