*{
    box-sizing: border-box;
}
body{
    margin: 0;
    padding: 0;
}
.container{
    max-width: 1170px;
    width: 100%;
    margin: 0 auto;
}
$menu-color:#585858;
$red-color:#e5493a;

.panel{
    background-color: #111111;
}
.panel-menu{
    padding: 15px 0 15px 0;
    display: flex;
    justify-content: space-between;
}
  .right-parth{
      display: flex;
   
      .panel-email{
        font-family: 'Roboto';
        font-size: 13px;
        .panel-mail{
            text-decoration: none; 
        }
        .email-adres{
            margin: 5px;
            color: $menu-color; 
        }
        .fas.fa-paper-plane{
            color: $menu-color; 
        }
      }
      .panel-telephone{
          margin: 0 50px 0 50px;
          font-family: 'Roboto';
            font-size: 13px;
        .panel-number{
            text-decoration: none;
        }
        .fas.fa-mobile{
            color: $menu-color;
        }
        .telephone-number {
            margin: 5px;
            color: $menu-color;
        }
      }
    .dropdown {
        position: relative;
        display: inline-block;
        &:hover{
            .dropbtn {
                
                background-color: #111111;
            }
            .dropdown-content{
                display: block;
            }
        }
        &-link{
            color: $menu-color;
            padding: 12px 12px;
            text-decoration: none;
            display: block;
            &:hover{
                background-color: #f1f1f1   
            }
        }
        &-btn {
            font-family: 'Roboto';
            font-size: 13px;
            color: $menu-color;
            
            background-color: #111111;
            border: none;
            cursor: pointer;
            .dropdown-text{
                margin: 5px;
            }
          }
          .dropdown-content {
            display: none;
            position: absolute;
            background-color: #111111;
            min-width: 160px;
            box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
            z-index: 1;
            
          }
      }
  }
  
