  .navbar{
      padding: 25px 0 25px 0;
      background-color: #252525;
      .container{
          display: flex;
          justify-content: space-between;
          align-items: center;
      
          .basket{
           
            .shoop-link{
                font-size: 16px;
            }
          }
  
          
          
         
          .navbar-panel{
         
            #nav-icon4 {
                width: 60px;
                height: 45px;
                position: relative;
               // margin: 50px auto;
               display: none;
                -webkit-transform: rotate(0deg);
                -moz-transform: rotate(0deg);
                -o-transform: rotate(0deg);
                transform: rotate(0deg);
                -webkit-transition: .5s ease-in-out;
                -moz-transition: .5s ease-in-out;
                -o-transition: .5s ease-in-out;
                transition: .5s ease-in-out;
                cursor: pointer;
            }
             #nav-icon4 span {
                display: block;
                position: absolute;
                height: 9px;
                width: 100%;
                background: $red-color;
                border-radius: 9px;
                opacity: 1;
                left: 0;
                -webkit-transform: rotate(0deg);
                -moz-transform: rotate(0deg);
                -o-transform: rotate(0deg);
                transform: rotate(0deg);
                -webkit-transition: .25s ease-in-out;
                -moz-transition: .25s ease-in-out;
                -o-transition: .25s ease-in-out;
                transition: .25s ease-in-out;
             }
            #nav-icon4 span:nth-child(1) {
              top: 0px;
              -webkit-transform-origin: left center;
              -moz-transform-origin: left center;
              -o-transform-origin: left center;
              transform-origin: left center;
            }
            
            #nav-icon4 span:nth-child(2) {
              top: 18px;
              -webkit-transform-origin: left center;
              -moz-transform-origin: left center;
              -o-transform-origin: left center;
              transform-origin: left center;
            }
            
            #nav-icon4 span:nth-child(3) {
              top: 36px;
              -webkit-transform-origin: left center;
              -moz-transform-origin: left center;
              -o-transform-origin: left center;
              transform-origin: left center;
            }
            
            #nav-icon4.open span:nth-child(1) {
              -webkit-transform: rotate(45deg);
              -moz-transform: rotate(45deg);
              -o-transform: rotate(45deg);
              transform: rotate(45deg);
              top: -3px;
              left: 8px;
            }
            
            #nav-icon4.open span:nth-child(2) {
              width: 0%;
              opacity: 0;
            }
            
            #nav-icon4.open span:nth-child(3) {
              -webkit-transform: rotate(-45deg);
              -moz-transform: rotate(-45deg);
              -o-transform: rotate(-45deg);
              transform: rotate(-45deg);
              top: 39px;
              left: 8px;
            }
           
         
         
                      

            text-transform: uppercase;
            display: flex;
            max-width: 840px;
            width: 100%;
            justify-content: space-between;
            .nav-link{
               
               &:hover{
                   color: $red-color;
                   
                    
               }
               
                font-family: 'Raleway-Bold';
                font-size: 14px;
                color: $menu-color;
                text-decoration: none;
            }
        }
        .nav-dropdown{
            position: relative;
            display: inline-block;
            &:hover{
                .nav-dropdown-btn{
                    color: $red-color;
                    &::before{
                        position: absolute;
                        top: -5px;
                        left: 0;
                        content: '';
                 
                       height: 2px;
                       width: 15px;
                       background: $red-color;
                      }
                }
                .nav-dropdown-content{
                    display: block;
                    
                }
            }
            &-link{
                font-family: 'Raleway-Bold';
                font-size: 14px;
                color: $menu-color;
                padding: 12px 16px;
                text-decoration: none;
                display: block;
                &:hover{
                    background-color: #f1f1f1  ; 
                }
            }
            &-btn {
                font-family: 'Raleway-Bold';
                font-size: 14px;
                color: $menu-color;
                
                border: none;
                cursor: pointer;
              }
              .nav-dropdown-content {
                display: none;
                color: $menu-color;
                position: absolute;
                background-color: #111111;
                min-width: 160px;
                box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
                z-index: 1;
                
              }
        }
      }
    .logo{
        &-name{
            font-family: 'Roboto-Bold';
            font-size: 56px;
            color: #ffffff;  
        }
    }
  }

.progress-container {
    width: 100%;
    height: 8px;
    position: fixed;
    z-index: 1;
    top: 0;
    background: #ccc;
    .progress-bar {
        height: 8px;
        background: $red-color;
        width: 0%;
      }
  }
  
.main-slider{
    .slick-dotted.slick-slider{
        margin: 0;
    }
    .single-item{
        .slick-dots {
            bottom: 0;
        }
        .slick-dots li.slick-active button:before{
            color :$red-color;
            
        }
        .single_wrap{
            background-image: url(../img/slider.png);
             background-repeat: no-repeat;
            background-size: cover;
        }
        .slider-block{

            max-width: 580px;
            width: 100%;
            margin-left:  auto  ;
            padding-bottom: 270px;
            padding-top: 265px;
            .slider-title {
                font-family: 'Raleway-Bold';
                font-size: 80px;
                margin: 0;
                color: #ffffff;
                text-transform: uppercase;
            }
            .slider-subtitle.color{
                color: $red-color;
                margin: 10px;
            }
            .slider-subtitle{
                font-family: 'Raleway-Bold';
                font-size: 43px;
                margin: 0;
                color: #ffffff;
                text-transform: uppercase;
                
            }
            .slider-text {
                font-family: 'Raleway-Bold';
                font-size: 18px; 
                color: #555555;
                text-transform: uppercase;
                margin-bottom: 40px;
            }
            .slider-butt-buy{
                color: #ffffff;
                border-radius: 5px;
                text-decoration: none;
                font-family: 'Raleway-Bold';
                font-size: 14px; 
                padding: 20px 30px 20px 30px;
                text-transform: uppercase;
                background-color: $red-color;
                margin-right:20px;
            }
            .slider-butt-live{
                
                color: #ffffff;
                border-radius: 5px;
                text-decoration: none;
                font-family: 'Raleway-Bold';
                font-size: 14px; 
                padding: 20px 30px 20px 30px;
                text-transform: uppercase;
                border: 1px solid #ffffff;
            }
        }
        
            
    }
}

.slider-navigation{
    
    display: flex;
    justify-content: space-between;
    .prev.slick-arrow{
        color: white;
        padding: 10px 10px 10px 10px;
        background-color: $menu-color;
        position: absolute;
        left: 0;
        top: 60%;
        &:hover{
            background-color: $red-color;
        }
    }
    .next.slick-arrow{
        color: white;
        padding: 10px 10px 10px 10px;
        background-color: $menu-color;
        position: absolute;
        right: 0;
        top: 60%;
        &:hover{
            background-color: $red-color;
        }
    }
}
