        margin: 0 20px;
    }
    
    .slick-prev.slick-arrow{
        color: white;
        padding: 15px 15px 15px 15px;
        background-color: $menu-color;
        right: -30px;
        left: -45px;
        border-radius: 5px;
        &::before{
            content: url(../img/arrow.png);
            top: 4px;
            left: 10px;
            position: absolute;
              transform: rotate(180deg);
        }
        &:hover{
            background-color: $red-color;
        }
    }
    .slick-next.slick-arrow{
        color: white;
        padding: 15px 15px 15px 15px;
        background-color: $menu-color;
        right: -30px;
      
        border-radius: 5px;
        &::before{
            content: url(../img/arrow.png);
            top: 4px;
            left: 10px;
            position: absolute;
        }
        &:hover{
            background-color: $red-color;
        }
    }
    .slick-slide{
        margin: 0 20px;
    }
    .slick-slide.slick-current.slick-active.slick-center{
        
        .slider-img{
          
            .slider-img-logo{
                opacity: 1;
            }
        }
    }
    .slick-track{
        margin: 20px;
        display: flex;
        align-items: center;
    }
        .slider-img{
